.root {
  backdrop-filter: blur(20px);
  display: block;
  padding: 20px;
  margin: 15px;
  box-sizing: border-box;
  box-shadow: 0px 3px 4px 3px rgb(162 162 162);
  border-radius: 3px;
  height: 100%;
  .title {
    font-weight: bold;
    font-size: 1.8rem;
    color: #fff;
  }
  .stepsPanel {
    padding: 15px;
    overflow-y: scroll;
    height: 70%;
  }
}

@media (min-width: 1400px) {
  .root {
    .stepsPanel {
      height: 80%;
    }
  }
}
