.root {
  display: flex;
  justify-content: space-between;
  border: 1px solid #fff;
  margin-top: 1rem;
  border-radius: 10px;
  padding: 0.5rem;
  :global(.slotContainer) {
    font-size: 1.2rem;
    margin: 0.5rem;
  }
  .slotKey {
    color: cornflowerblue;
  }
  .slotValue {
    color: darkkhaki;
  }
}
